import "./styles.css";
import React from "react";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null
    };
    this.handleChangeCat = this.handleChangeCat.bind(this);
    this.handleChangeName = this.handleChangeName.bind(this);
  }

  componentDidMount() {
    fetch("https://60min.karlova25.cz/br/getrecords_u10d.php")
      .then((response) => response.json())
      .then((data) => this.setState({ data }));
  }

  handleChangeCat(rec) {
    //console.log(rec.target.name);
    //console.log(rec.target.value);
    if (rec.target.value) {
      const requestOptions = {
        method: "POST",
        //headers: { 'Content-Type': 'application/json'},
        body: JSON.stringify({
          id: rec.target.name,
          cat: rec.target.value
        })
      };
      const response = fetch(
        "https://60min.karlova25.cz/br/setcat.php",
        requestOptions
      );
    }
  }

  handleChangeName(rec) {
    console.log(rec.target.name);
    console.log(rec.target.value);
    if (rec.target.value) {
      const requestOptions = {
        method: "POST",
        //headers: { 'Content-Type': 'application/json'},
        body: JSON.stringify({
          id: rec.target.name,
          name: rec.target.value
        })
      };
      const response = fetch(
        "https://60min.karlova25.cz/br/setname.php",
        requestOptions
      );
      console.log(response);
    }
  }

  render() {
    //console.log(this.state.data);
    if (this.state.data) {
      var list = (
        <>
          {this.state.data.map((record) => (
            <Grid item key={record.Id}>
              <Paper>
                <Grid item xs={12}>
                  <img
                    style={{ maxHeight: "80vh" }}
                    src={"https://60min.karlova25.cz/br/images/" + record.Image}
                    alt="logo"
                  />
                  <h5>
                    ID: {record.Id}
                    <br />
                    {record.Category}
                    <br />
                    {record.Name}
                  </h5>
                  <h5>
                    Oprava kategorie:{" "}
                    <input
                      placeholder={record.CategoryEdit}
                      name={record.Id}
                      onChange={this.handleChangeCat}
                    />
                    <br />
                    Oprava jm√©na:{" "}
                    <input
                      placeholder={record.NameEdit}
                      name={record.Id}
                      onChange={this.handleChangeName}
                    />
                    <br />
                  </h5>
                </Grid>
              </Paper>
            </Grid>
          ))}
        </>
      );
    }

    return (
      <div className="App">
        <Grid
          container
          align="center"
          justify="center"
          alignItems="center"
          spacing={2}
        >
          {list}
        </Grid>
      </div>
    );
  }
}
